import React from "react";

const TestimonialCard = ({ name, title, testimonial, link }) => {
	return (
		<div className="TestimonialCard">
			<div className="TestimonialCard__user-picture"></div>
			<div className="TestimonialCard__description">
				{testimonial || "Description Something"}
			</div>
			<div className="TestimonialCard__name">
				{name || "Default Name"}
			</div>
			<div className="TestimonialCard__title">Software Engineer</div>
		</div>
	);
};

export default TestimonialCard;
