import React from "react";
import { Container } from "react-bootstrap";
import "./Contact.css";

function Contact() {
	return (
		<Container className="Contact">
			<div className="Contact__container">
				<div className="Contact__image"></div>
				<div className="Contact__form">
					<div className="Contact__form-label">Contact Form</div>
					<div className="Contact__input-form">
						<div className="Contact__input-label">Name</div>
						<input type="text" className="Contact__input" />
					</div>
					<div className="Contact__input-form">
						<div className="Contact__input-label">Email</div>
						<input type="text" className="Contact__input" />
					</div>
					<div className="Contact__input-form">
						<div className="Contact__input-label">Job Types</div>
						<input type="text" className="Contact__input" />
					</div>
					<div className="Contact__input-form">
						<div className="Contact__input-label">Messages</div>
						<input type="text" className="Contact__input" />
					</div>
					<div className="Contact__btn-submit">Submit</div>
				</div>
			</div>
		</Container>
	);
}

export default Contact;
